:root {
  --text-color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "RF Dewi";
  font-weight: normal;
  font-size: 16px;
  color: var(--text-color);
}

img {
  width: 100%;
  display: block;
  object-fit: cover;
}

h1 {
  font-family: "RF Dewi Expanded";
  font-weight: bold;
}

button {
  font-family: "RF Dewi Expanded";
  font-style: normal;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  max-width: 1300px;
  width: 100%;
}

.title {
  font-family: "RF Dewi Expanded";
  font-style: normal;
  font-weight: 700;
  font-size: 45px;
  line-height: 129.19%;
  color: #FFFFFF;
}

.text__color {
  color: #FFBF44;
}

/*HEADER*/
.header .container {
  position: relative;
  z-index: 160;
}
.header__video-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  height: 100%;
  width: 100%;
  z-index: 100;
}
.header__bg {
  width: 100%;
  position: relative;
  filter: brightness(70%);
  z-index: 90;
}
.header__img-bg {
  position: absolute;
  top: 0;
  left: 0;
  filter: brightness(50%);
  height: 100vh;
}
.header__wrapper {
  max-width: 1071px;
  width: 100%;
  margin: 0 auto;
}
.header__title {
  text-align: center;
  margin-top: 260px;
}
.header__subtitle {
  font-style: normal;
  font-weight: 600;
  font-size: 20.395px;
  line-height: 129.19%;
  text-align: center;
  margin-top: 25px;
}
.header__btn-box {
  max-width: 550px;
  margin: 0 auto;
  padding-top: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__btn {
  padding: 18px 30px;
  font-size: 18px;
  background-color: rgba(255, 255, 255, 0.534);
  box-shadow: 0px 0px 8px rgb(255, 255, 255);
}
.header__btn:active {
  box-shadow: 0px 0px 10px rgb(255, 255, 255);
}
.header__btn:hover {
  background-color: rgba(255, 255, 255, 0.918);
}
.header__btn-link {
  padding: 18px 30px;
  color: var(--text-color);
  font-family: "RF Dewi Expanded";
}

.nav {
  background-color: rgba(0, 0, 0, 0.712);
  width: 100%;
  z-index: 1500;
  position: fixed;
  top: 0;
  left: 0;
}
.nav__wrapper {
  display: flex;
  max-width: 1300px;
  width: 100%;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
}
.nav__btn {
  color: var(--text-color);
  background-color: rgba(255, 255, 255, 0);
}

.logo {
  margin: 26px 0;
}

.menu {
  display: flex;
  list-style: none;
}
.menu__item {
  margin-left: 30px;
}
.menu__link {
  margin-left: 30px;
  color: #000;
  padding: 10px 25px;
  background-color: rgba(255, 255, 255, 0.534);
  box-shadow: 0px 0px 8px rgb(255, 255, 255);
}
.menu__link:active {
  box-shadow: 0px 0px 10px rgb(255, 255, 255);
}
.menu__link:first-child {
  margin-left: 0;
}
.menu__link:hover {
  background-color: rgba(255, 255, 255, 0.918);
}

.mobmenu {
  display: none;
  position: fixed;
  z-index: 5000;
  top: 66px;
  background-color: rgb(0, 0, 0);
  width: 100%;
  left: -100%;
  transition: all 0.5s;
}
.mobmenu__list {
  padding-left: 15%;
  padding-bottom: 20px;
  list-style: none;
}
.mobmenu__item {
  padding-top: 30px;
  border-bottom: 1px solid #fff;
}
.mobmenu__item:hover {
  background-color: rgb(255, 255, 255);
}
.mobmenu__item:hover .mobmenu__link {
  color: #000000;
}
.mobmenu__link {
  color: var(--text-color);
  text-transform: uppercase;
  font-size: 24px;
}
.mobmenu__btn {
  display: none;
  content: "";
  width: 25px;
  height: 2px;
  background-color: rgb(255, 255, 255);
  cursor: pointer;
}
.mobmenu__btn::before, .mobmenu__btn::after {
  display: block;
  content: "";
  width: 25px;
  height: 2px;
  background-color: #fff;
}
.mobmenu__btn::before {
  margin-top: 7px;
}
.mobmenu__btn::after {
  margin-top: -16px;
}
.mobmenu__show {
  left: 0;
  transition: all 0.5s;
}

/*END HEADER*/
