@font-face {
    font-family: 'RF Dewi Expanded';
    src: url('../fonts/RFDewiExpanded-Bold.eot');
    src: local('RF Dewi Expanded Bold'), local('RFDewiExpanded-Bold'),
        url('../fonts/RFDewiExpanded-Bold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/RFDewiExpanded-Bold.woff2') format('woff2'),
        url('../fonts/RFDewiExpanded-Bold.woff') format('woff'),
        url('../fonts/RFDewiExpanded-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'RF Dewi';
    src: url('../fonts/RFDewi-Regular.eot');
    src: local('RF Dewi Regular'), local('RFDewi-Regular'),
        url('../fonts/RFDewi-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/RFDewi-Regular.woff2') format('woff2'),
        url('../fonts/RFDewi-Regular.woff') format('woff'),
        url('../fonts/RFDewi-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'RF Dewi';
    src: url('../fonts/RFDewi-Semibold.eot');
    src: local('RF Dewi Semibold'), local('RFDewi-Semibold'),
        url('../fonts/RFDewi-Semibold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/RFDewi-Semibold.woff2') format('woff2'),
        url('../fonts/RFDewi-Semibold.woff') format('woff'),
        url('../fonts/RFDewi-Semibold.ttf') format('truetype');
    font-weight: 600;
    font-style: normal;
}

@font-face {
    font-family: 'RF Dewi';
    src: url('../fonts/RFDewi-Bold.eot');
    src: local('RF Dewi Bold'), local('RFDewi-Bold'),
        url('../fonts/RFDewi-Bold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/RFDewi-Bold.woff2') format('woff2'),
        url('../fonts/RFDewi-Bold.woff') format('woff'),
        url('../fonts/RFDewi-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

:root{
    --text-color: #ffffff;
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    text-decoration: none;
}

html{
    scroll-behavior: smooth;
}

body {
    font-family: 'RF Dewi';
    font-weight: normal;
    font-size: 16px;
    color: var(--text-color);
}

img{
    width: 100%;
    display: block;
    object-fit: cover;
}

h1 {
    font-family: 'RF Dewi Expanded';
    font-weight: bold;
}

button{
    font-family: 'RF Dewi Expanded';
    font-style: normal;
    cursor: pointer;
}

.container {
    margin: 0 auto;
    max-width: 1300px;
    width: 100%;
}

.title{
    font-family: 'RF Dewi Expanded';
    font-style: normal;
    font-weight: 700;
    font-size: 45px;
    line-height: 129.19%; 
    color: #FFFFFF;    
}

.text__color{
    color: #FFBF44;
}


/*HEADER*/
.header {
    & .container {
        position: relative;
        z-index: 160;
    }

    &__video-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        height: 100%;
        width: 100%;
        z-index: 100;
    }

    &__bg {
        width: 100%;
        position: relative;
        filter: brightness(70%);
        z-index: 90;
    }

    &__img-bg {
        position: absolute;
        top: 0;
        left: 0;
        filter: brightness(50%);
        height: 100vh;
    }

    &__wrapper{
        max-width: 1071px;
        width: 100%;
        margin: 0 auto;
    }

    &__title {
        text-align: center;
        margin-top: 260px;
    }

    &__subtitle {
        font-style: normal;
        font-weight: 600;
        font-size: 20.395px;
        line-height: 129.19%;
        text-align: center;
        margin-top: 25px;
    }

    &__btn-box {
        max-width: 550px;
        margin: 0 auto;
        padding-top: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__btn {
        padding: 18px 30px;
        font-size: 18px;
        background-color: rgba(255, 255, 255, 0.534);
        box-shadow: 0px 0px 8px rgb(255, 255, 255);

        &:active {
            box-shadow: 0px 0px 10px rgb(255, 255, 255);
        }

        &:hover {
            background-color: rgba(255, 255, 255, 0.918);
        }
    }

    &__btn-link {
        padding: 18px 30px;
        color: var(--text-color);
        font-family: 'RF Dewi Expanded';
    }
}

.nav {
    background-color: rgba(0, 0, 0, 0.712);
    width: 100%;
    z-index: 1500;
    position: fixed;
    top: 0;
    left: 0;

    &__wrapper {
        display: flex;
        max-width: 1300px;
        width: 100%;
        margin: 0 auto;
        align-items: center;
        justify-content: space-between;
    }

    &__btn {
        color: var(--text-color);
        background-color: rgba(255, 255, 255, 0);
    }
}

.logo {
    margin: 26px 0;
}

.menu {
    display: flex;
    list-style: none;
    &__item{
        margin-left: 30px;
    }
    &__link {
        margin-left: 30px;
        color: #000;
        padding: 10px 25px;
        background-color: rgba(255, 255, 255, 0.534);
        box-shadow: 0px 0px 8px rgb(255, 255, 255);

        &:active {
            box-shadow: 0px 0px 10px rgb(255, 255, 255);
        }

        &:first-child {
            margin-left: 0;
        }

        &:hover {
            background-color: rgba(255, 255, 255, 0.918);
        }
    }
}


.mobmenu {
    display: none;
    position: fixed;
    z-index: 5000;
    top: 66px;
    background-color: rgb(0, 0, 0);
    width: 100%;
    left: -100%;
    transition: all 0.5s;
    &__list {
        padding-left: 15%;
        padding-bottom: 20px;
        list-style: none;
    }
    &__item {
        padding-top: 30px;
        border-bottom: 1px solid #fff;
        &:hover{
            background-color: rgb(255, 255, 255);
            .mobmenu__link{
                color: #000000;
            }
        }
    }
    &__link {
        color: var(--text-color);
        text-transform: uppercase;
        font-size: 24px;
    }
    &__btn{
        display: none;
        content: '';
        width: 25px;
        height: 2px;
        background-color: rgb(255, 255, 255);
        cursor: pointer;
        &::before,
        &::after{
            display: block;
            content: '';
            width: 25px;
            height: 2px;
            background-color: #fff;
        }
        &::before{
            margin-top: 7px;
        }
        &::after{
            margin-top: -16px;
        }
    }
    &__show{
        left: 0;
        transition: all 0.5s;
    }
}

/*END HEADER*/


/*ABOUT*/
.about {
    width: 100%;
    background-color: rgb(78, 72, 72);
    background-image: url(../img/about-bg.png);
    background-size: cover;
    background-position: 0 0;
    padding-top: 20%;
    padding-bottom: 115px;

    &__box {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        width: 50%;

        &:nth-child(1) {
            padding-right: 40px;
        }

        &:nth-child(2) {
            padding: 150px 0 150px 40px;
        }

        &:nth-child(3) {
            padding: 125px 40px 0 0;
        }

        &:nth-child(4) {
            padding: 125px 0 0 40px;
        }
    }

    &__subtitle {
        font-family: 'RF Dewi Expanded';
        font-style: normal;
        font-weight: 700;
        font-size: 26px;
        line-height: 31px;
        margin-top: 30px;
        padding-right: 10%;
    }

    &__text {
        margin-top: 20px;
        padding-right: 30%;
    }

    &__text,
    &__list-item {
        font-family: 'RF Dewi';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        opacity: 0.6;
    }

    &__list {
        margin-top: 20px;
        list-style-image: url(../img/list-marker.svg);
    }

    &__list-item {
        padding-top: 15px;
        margin-left: 20px;
    }

    &__box-btn {
        margin-top: 50px;
    }

    &__btn {
        padding: 15px 25px;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%), rgba(255, 255, 255, 0.3);
        border-radius: 20px;
        color: var(--text-color);
        border: 2px solid #fff;
        font-size: 16px;
        line-height: 157.4%;
        color: #FFFFFF;

        &:hover {
            box-shadow: 0px 0px 5px rgb(255, 255, 255);
        }

        &:active {
            box-shadow: 0px 0px 10px rgb(255, 255, 255);
        }
    }

    &__link {
        margin-left: 40px;
        color: var(--text-color);
        font-family: 'RF Dewi Expanded';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 157.4%;
    }

    &__video-box {
        position: relative;
    }

    &__video {
        width: 493px;
        position: absolute;
        top: 15px;
        left: 61px;
    }
}

/*END ABOUT*/


/*ADVANTAGES*/
.advantages {
    width: 100%;
    background-image: url(../img/avantages-bg.png);
    background-color: rgb(78, 72, 72);
    background-size: cover;
    background-repeat: no-repeat;
    padding-bottom: 105px;
    position: relative;

    &__title {
        padding-top: 66px;
        padding-bottom: 25px;
        text-align: center;
        z-index: 22;
        position: relative;
    }

    &__video-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 20px;
        width: 100%;
        overflow: hidden;
        height: 690px;
    }

    &__video {
        width: 100%;
    }
}

.box {
    z-index: 24;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1023px;
    height: 416px;
    width: 100%;
    margin: 0 auto;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%), rgba(0, 0, 0, 0.3);
