            background: #FFBF44;
            box-shadow: 0px 17px 17px rgba(255, 191, 67, 0.28);
            border-radius: 12px; 
        }
    }

    &__img-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background: #2BE3A0;
        box-shadow: 0px 17px 17px rgba(43, 227, 160, 0.28);
        border-radius: 12px; 
        width: 64px;
        height: 64px;
        margin: 0 auto;
    }

    &__img-bg {
        width: 80px;
    }

    &__img {
        width: 25px;
    }

    &__text {
        margin-top: 30px;
    }
}


/*END ADVANTAGES*/


