/*FEEDBACK*/
.feedback {
    width: 100%;
    background-color: rgb(78, 72, 72);
    position: relative;
    padding: 86px 0 70px 0;
    height: 374px;

    & .container {
        display: flex;
        z-index: 1000;
        position: relative;
    }

    &__item {
        &:first-of-type {
            width: 60%;
        }

        &:last-child {
            width: 40%;
        }
    }

    &__title {
        font-family: 'RF Dewi Expanded';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        line-height: 43px;
        color: #FFFFFF;
    }

    &__subtitle {
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        opacity: 0.6;
        margin-top: 30px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        max-width: 343px;
        width: 100%;
    }

    &__input {
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%), rgba(0, 0, 0, 0.3);
        backdrop-filter: blur(2px);
