        background: #FFFFFF;
        font-family: 'RF Dewi Expanded';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        color: #000000;
        padding: 15px 44px;
    }

    &__bg {
        width: 100%;
        position: absolute;
        top: 0;
        filter: brightness(0.4);
        height: 100%;
        z-index: 10;
    }
}

/*END FEEDBACK*/

