        font-weight: 600;
        font-size: 18.1289px;
        line-height: 129.19%;
        color: #FFFFFF;
    }

    &__logo {
        width: 260px;
    }

    &__box {
        display: flex;
    }

    &__copyright {
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        color: #FFFFFF;
        opacity: 0.4;
    }

    &__icon {
        width: 22px;
        height: 22px;
        margin-left: 20px;

        &:last-child {
            margin-left: 10px;
        }
    }

    &__link {
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        color: #FFFFFF;
        opacity: 0.4;

        &:last-child {
            margin-left: 30px;
        }
    }
}
/*END FOOTER*/

/*MODAL*/
.modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: rgba(0, 0, 0, .5);

    &__dialog {
        max-width: 530px;
        margin: 150px auto;
    }

    &__content {
        position: relative;
        width: 100%;
        padding: 40px;
        background-color: #fff;
        border: 1px solid rgba(0, 0, 0, 0.473);
        border-radius: 4px;
        max-height: 80vh;
        overflow-y: auto;

        &-title {
            font-size: 24px;
            font-weight: 600;
            padding-bottom: 20px;
            text-align: center;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
    }

    &__close {
        position: absolute;
        top: 8px;
        right: 14px;
        font-size: 30px;
        color: #000;
        opacity: .5;
        font-weight: 700;
        border: none;
        background-color: transparent;
        cursor: pointer
    }

    &__input {
        border: 1px solid rgb(238, 238, 238);
        padding: 20px;
        margin-bottom: 20px;
    }

    &__btn {
