/*MOTO*/
.moto {
    width: 100%;
    background-image: url(../img/moto__bg.png);
    background-color: rgb(78, 72, 72);
    background-size: cover;
    padding-top: 25%;
    padding-bottom: 92px;

    & .container {
        display: flex;
        align-items: flex-end;
    }

    &__item {
        &:first-child {
            width: 55%;
            padding: 44px 32px 47px 53px;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%), rgba(0, 0, 0, 0.3);
            backdrop-filter: blur(6px);
            border-radius: 42px;
        }

        &:last-child {
            width: 45%;
            position: relative;
        }
    }

    &__title {
        font-family: 'RF Dewi Expanded';
        font-style: normal;
        font-weight: 700;
        font-size: 26px;
        line-height: 31px;
    }

    &__list {
        margin-top: 34px;
        list-style: none;
        list-style-image: url(../img/list-marker.svg);
    }

    &__list-item {
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        padding-bottom: 30px;
    }

    &__box {
        display: flex;
        align-items: center;
    }

    &__wrapper {
        padding-left: 34px;
    }

    &__elllips {
        position: relative;
        width: 463.12px;
    }

    &__moped {
        position: absolute;
        top: 0;
        width: 500px;
    }
}

/* END MOTO*/


/*BENEFITS*/
.benefits {
    background-image: url(../img/benefits.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgb(78, 72, 72);
    padding: 66px 0 105px 0;

    &__title {
        text-align: center;
        margin-bottom: 25px;
    }
}

/*END BENEFITS*/

/*QUESTION*/
.questions {
    width: 100%;
    background-image: url(../img/reviews-bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-color: rgb(78, 72, 72);
    padding: 69px 0 138px 0;
    &__wrapper{
        padding-top: 20%;
    }
    &__title {
        text-align: center;
        margin-bottom: 44px;
    }
    &__box{
        border-radius: 41px;
        overflow: hidden;
    }
    &__quest {
        font-family: 'RF Dewi Expanded';
        font-style: normal;
        font-weight: 700;
        font-size: 26px;
        line-height: 31px;   
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%), rgba(0, 0, 0, 0.3);
        backdrop-filter: blur(2px);
        padding: 26px 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &__color{
            color: #E9BC47;
        } 
        &::after{
            content: '';
            display: block;
            background-image: url(../img/quertion__arrow.svg);
            background-repeat: no-repeat;
            background-position: right;
            width: 55px;
            height: 21px;
            cursor: pointer;
        }
    }

    &__answer {
        font-family: 'RF Dewi Expanded';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;   
        padding: 41px 50px 41px 50px; 
        background: linear-gradient(0deg, rgba(233, 188, 71, 0.02), rgba(233, 188, 71, 0.02)), linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%), rgba(0, 0, 0, 0.18);
        backdrop-filter: blur(2px);
    }
}
.show{
    display: block;
}

.hide{
    display: none;
}
  
.fade{
    animation-name: fade;
    animation-duration: 1.5s;
}
  
@keyframes fade{
    from{
        opacity: 0.1;
    }
    to{
        opacity: 1;
    }
}

/*END QUESTION*/

